.header {
  padding-top: 48px;
  padding-bottom: 97px;
  margin-left: auto;
  margin-right: auto;
  transition: background-color .9s, background-image .9s;
  background: rgba(0, 0, 0, 0.56);
  background-image: url('/src/images/mobile/m-home-1X.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
    
    @include mq ('retina') {
      background-image: url('/src/images/mobile/m-home-2X.jpg');
    }

  @include mq ('tablet') {
    background-image: url('/src/images/tablet/t-home-1X.jpg');
    
      @include mq ('retina') {
        background-image: url('/src/images/tablet/t-home-2X.jpg');
      }
  }
  
  @include mq ('desktop') {
    background-image: url('/src/images/desktop/d-home-1X.jpg');

    @include mq ('retina') {
      background-image: url('/src/images/desktop/d-home-2X.jpg');
    }
  }
}

.header-library {
  padding-top: 48px;
  padding-bottom: 65px;
  margin-left: auto;
  margin-right: auto;
  background: rgba(0, 0, 0, 0.56);
  background-image: url('/src/images/mobile/m-library-1X.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: background-color .9s, background-image .9s;
  @include mq ('retina') {
    background-image: url('/src/images/mobile/m-library-2X.jpg');
  }

  @include mq ('tablet') {
    background-image: url('/src/images/tablet/t-library-1X.jpg');

    @include mq ('retina') {
      background-image: url('/src/images/tablet/t-library-2X.jpg');
    }
  }

  @include mq ('desktop') {
    background-image: url('/src/images/desktop/d-library-1X.jpg');

    @include mq ('retina') {
      background-image: url('/src/images/desktop/d-library-2X.jpg');
    }
  }
}


.header__title {
  display: none;

    @include mq ('tablet') {
      display: contents;
      font-weight: 500;
      font-size: 30px;
      line-height: 35px;
      align-items: center; 
      text-align: center;
    }  
}

.logo {
    margin-bottom: 45px;
    @include mq ('tablet') {
      margin-bottom: 0;
      margin-right: 10px;
    }
}

@include mq ('tablet') {
  .header__logo {
    margin-bottom: 37px;
  }
}